<template>
    <v-container fluid>
        <v-app-bar
                fixed
                app
                color="#FFFFFF"
                elevation="3"
                class="app-bar-gradient-back"
                dark
        >
            <v-row>
                <!--<v-col></v-col>-->
                <v-col class="text-center">
                    <v-toolbar-title>{{chatsText}}</v-toolbar-title>
                </v-col>
                <!-- <v-col class="text-right" style="padding-top: 0; padding-bottom: 0"><v-btn class="app-bar-button" small style="display: inline;" text color="primary"
                                                                                            @click="openProfileEdit">{{editText}}
                 </v-btn></v-col>-->
            </v-row>


        </v-app-bar>
        <div style="margin-top: 54px"/>
        <v-row v-if="numberOfChats > 0">
            <v-col class="pa-0">
                <v-list class="pa-3"
                        style="padding-left: 0px!important; padding-right: 0px!important; padding-top: 6px!important">
                    <v-text-field
                            style="padding: 10px; padding-bottom: 0px!important;"
                            v-model="searchByUsernameField"
                            :label="searchLabel"
                            solo
                            dense
                            clearable
                            rounded
                            @input="listChatsByUsername"
                    ></v-text-field>
                </v-list>
            </v-col>
        </v-row>
        <v-row style="margin-top: -10px">
            <v-col class="pa-0">
                <v-list three-line class="ma-0 pa-0">
                    <v-divider></v-divider>
                    <template v-for="(item, index) in items">
                        <v-list-item
                                :key="item.title"
                                @click="openChatWithUser(item.ids)"
                        >
                            <v-list-item-avatar size="50px">
                                <v-img :src="item.avatar" style="border: 2px solid #00abba;"></v-img>
                            </v-list-item-avatar>

                            <v-list-item-content>
                                <v-list-item-title class="secondary--text">
                                    {{item.title}}
                                    <span v-show="item.notReadMessages != 0" id="notReadMessagesNotification">{{item.notReadMessages}}</span>
                                </v-list-item-title>
                                <v-list-item-subtitle v-html="item.subtitle"></v-list-item-subtitle>
                            </v-list-item-content>
                        </v-list-item>
                        <v-divider :key="index"></v-divider>
                    </template>
                </v-list>
            </v-col>
        </v-row>

        <v-card v-if="numberOfChats == 0 && (searchByUsernameField == null || searchByUsernameField == '')"
                class="pa-2 ma-1"
                style="padding-bottom: 24px!important; height: calc(100vh - 260px); padding-top: 24px!important; margin-top: 30px!important;">
            <v-row class="text-center justify-center">
                <p class="pa-4" style="opacity: 0.6; padding-bottom: 0px!important">{{noChatsText}}</p>
            </v-row>
            <v-row class="text-center justify-center">
                <img :src="sadFaceImg" style="opacity: 0.2; width: 22.5%; max-width: 150px; min-width: 65px">
            </v-row>
        </v-card>
        <v-card v-if="numberOfChats == 0 && searchByUsernameField != null && searchByUsernameField != ''"
                class="pa-2 ma-1"
                style="padding-bottom: 24px!important; height: calc(100vh - 260px); padding-top: 24px!important;">
            <v-row class="text-center justify-center">
                <p class="pa-4" style="opacity: 0.6; padding-bottom: 0px!important">{{noChatsMatchText}}</p>
            </v-row>
            <v-row class="text-center justify-center">
                <img :src="sadFaceImg" style="opacity: 0.2; width: 22.5%; max-width: 150px; min-width: 65px">
            </v-row>
        </v-card>
        <v-snackbar
                v-model="snackbar"
                :timeout="4000"
                style="bottom: 76px"
        >
            {{ snackbarText }}
            <v-btn
                    color="primary"
                    text
                    @click="snackbar = false"
            >
                {{closeText}}
            </v-btn>
        </v-snackbar>
    </v-container>
</template>

<script>
    import {mapGetters} from 'vuex';
    import Constants from "../../store/Constants";

    export default {
        name: "ChatHome",
        data() {
            return {
                chatsText: null,
                searchByUsernameField: null,
                noChatsText: null,
                sadFaceImg: null,
                searchLabel: null,
                snackbarText: null,
                snackbar: false,
                closeText: null,
                noChatsMatchText: null
            }
        },
        methods: {
            /*...mapActions([
                'prepareChatWithUser'
            ]),*/
            openChatWithUser(itemIDs) {
                this.$router.push('chats/chat/' + itemIDs.chat);
            },
            listChatsByUsername: function () {
                if (this.searchByUsernameField != null && this.searchByUsernameField != undefined && this.searchByUsernameField != '') {
                    this.$store.commit('chats/setUsernameFilter', this.searchByUsernameField);
                } else {
                    this.$store.commit('chats/setUsernameFilter', null);
                }
            }
        },
        created() {
            this.noChatsText = this.$i18n.t('messages.TEXT_NO_CHATS_AVAILABLE');
            this.searchLabel = this.$i18n.t('messages.TEXT_SEARCH');
            this.noChatsText = this.$i18n.t('messages.TEXT_NO_CHATS_AVAILABLE');
            this.chatsText = this.$i18n.t('messages.TEXT_CHATS');
            this.closeText = this.$i18n.t('messages.TEXT_CLOSE');
            this.noChatsMatchText = this.$i18n.t('messages.TEXT_NO_CHATS_MATCH_SEARCH');

            this.sadFaceImg = Constants.IMG_EMOJI_SAD_FACE;
        },
        computed: {
            ...mapGetters('chats', ['getChatsToShow', 'getNotReadMessagesCountByChatID', 'getUserByChatID']),

            items: function () {
                let self = this;
                let itemsChatList = [];

                self.getChatsToShow.forEach(chat => {

                    let user = self.getUserByChatID(chat.id);

                    itemsChatList.push({
                        ids: {
                            chat: chat.id,
                            user: user.id
                        },
                        avatar: user.profileImg,
                        title: user.username,
                        subtitle: chat.lastMessage.text,
                        notReadMessages: self.getNotReadMessagesCountByChatID(chat.id)
                    });
                });

                return itemsChatList;
            },
            numberOfChats: function () {
                return this.items.length;
            }
        }
    }
</script>

<style scoped>
    #notReadMessagesNotification {
        font-size: 12px;
        text-align: center;
        display: inline-block;
        background-color: #0ccfd1;
        color: white;
        margin-left: 5px;
        min-width: 22px !important;
        padding: 4px;
        border-radius: 50%;
    }
</style>